@import "../../main.scss";

.itemWrapper {
  position: absolute;
  background-color: $lightColor;
  color: $darkColor;
  width: 100%;
  height: 100%;
  top: 0;
  transition: transform 0.3s ease-out;
}

.headerWrapper {
  position: absolute;
  width: 100%;
  height: 22%;
  font-size: 32pt;
  font-family: "Assistant Light";
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.header {
  position: relative;
  width: 84%;
}

.detailsWrapper {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50%;
  top: 18%;
  font-family: "Assistant ExtraLight";
  font-size: 18pt;
}

.details {
  position: relative;
  left: 2%;
  width: 83%;
  height: 84%;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
}

.detail {
  position: relative;
  width: 90%;
  height: 20%;
}
.icon {
  position: absolute;
  width: 40px;
  height: 100%;
  mask-position: center;
  mask-size: contain;
  background-color: $darkColor;
  mask-repeat: no-repeat;
}

.infoWrapper {
  position: relative;
  width: 85%;
  height: 100%;
  left: 50px;
}

.info {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.descriptionWrapper {
  position: absolute;
  width: 100%;
  height: 40%;
  top: 60%;
}

.description {
  position: absolute;
  width: 54%;
  height: 90%;
  left: 12%;
  top: 70%;
  transform: translate(0, -50%);
  font-family: "Assistant ExtraLight";
  font-size: 16pt;
  text-align: right;
  direction: rtl;
}

.image {
  position: absolute;
  width: 40%;
  height: 90%;
  right: 0;
  mix-blend-mode: multiply;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
.return {
  position: absolute;
  width: 65px;
  height: 65px;
  left: 15px;
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}
