@import "../../main.scss";

.autoCompleteContainer {
  position: absolute;
  text-align: right;
  background-color: transparent;
  bottom: 40px;
  width: 70%;
  height: 45px;
  border-radius: 10px;
  left: 10%;
  border: 1px solid $lightColor;
  color: $lightColor;
  font-family: "Assistant Light";
  font-size: 46pt;
  z-index: 5;
}

.react-autosuggest__container {
  position: absolute;
  width: 100%;
  height: 100%;
  font-size: 24pt;
  background-color: transparent;
  font-family: "Assistant Light";
  right: 0;
  top: 0;
  text-align: right;
  border: none;

  &:focus {
    outline: none;
  }
}

.react-autosuggest__input {
  position: absolute;
  right: 4px;
  bottom: 0px;
  font-size: 24pt;
  width: 95%;
  height: 98%;
  background-color: transparent;
  border-radius: 10px;
  font-family: "Assistant Light";
  text-align: right;
  color: $lightColor;
  vertical-align: middle;
  border: none;
  &:focus {
    outline: none;
  }
}

.react-autosuggest__input--open {
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.react-autosuggest__suggestions-container {
  display: none;
}

.react-autosuggest__suggestions-container--open {
  display: block;
  position: absolute;
  bottom: 45px;
  width: 100%;
  background-color: $extraDarkColor;
  border: 0.5px solid $lightColor;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom: 0;
  color: $lightColor;
  z-index: 2;
}

.react-autosuggest__suggestions-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
}

.react-autosuggest__suggestion {
  cursor: pointer;
  border-bottom: solid 1px rgba($lightColor, 0.4);
  padding: 10px 10px;
}

.react-autosuggest__suggestion--highlighted {
  background-color: rgba($lightColor, 0.08);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  z-index: 0;
}

:placeholder-shown {
  color: rgba($lightColor, 0.4);
}

::-webkit-input-placeholder {
  color: rgba($lightColor, 0.4);
}

.searchIcon {
  position: absolute;
  width: 40px;
  height: 40px;
  right: -50px;
  top: 50%;
  transform: translateY(-50%) scale(1.1);
  background-color: $lightColor;
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: contain;
  cursor: pointer;
}
