@import "../../main.scss";

.categoryWrapper {
  position: absolute;
  background-color: $extraDarkColor;
  width: 100%;
  height: 100%;
  top: 0;
  transition: transform 0.5s ease-out;
}

.categoryHome {
  position: absolute;
  color: $darkColor;
  width: 100%;
  height: 100%;
  top: 0;
  transition: transform 0.3s ease-out;
}

.headerWrapper {
  position: absolute;
  width: 70%;
  height: 45%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -65%);
}

.header {
  position: absolute;
  bottom: 0;
  width: 100%;
  font-family: "Assistant ExtraLight";
  font-size: 70pt;
  text-align: center;
  color: $lightColor;
}

.image {
  position: absolute;
  top: 0;
  width: 200px;
  height: 200px;
  left: 50%;
  transform: translateX(-50%);
  background-size: 70%;
  background-position: center;
  background-repeat: no-repeat;
}
