@import "../../main.scss";

.homeWrapper {
  position: absolute;
  background-color: $extraDarkColor;
  width: 100%;
  height: 100%;
  top: 0;
  transition: transform 0.3s ease-out;
}

.headerWrapper {
  position: absolute;
  width: 70%;
  height: 45%;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -65%);
}

.header {
  position: absolute;
  top: 0;
  width: 100%;
  font-family: "Assistant ExtraLight";
  font-size: 70pt;
  text-align: center;
  color: $lightColor;
}

.image {
  position: absolute;
  top: 100pt;
  width: 200px;
  height: 200px;
  left: 50%;
  transform: translateX(-50%);
  mask-size: 200px;
  mask-position: center;
  mask-repeat: no-repeat;
  background-color: $lightColor;
}
.line {
  position: absolute;
  width: 70%;
  left: 50%;
  transform: translateX(-50%);
  height: 2px;
  top: 105px;
  background-color: $lightColor;
}
